export const STATE_REGULATIONS: StateRegulation[] = STATE_LIST.map(({ abbr, name }) => ({
  abbr,
  name,
  ...BASE_DEFAULT,
  ...STATE_OVERRIDES[abbr],
}));

export function getStateByAbbr(stateAbbr: string) {
  return STATE_REGULATIONS.find(
    (state) => state.abbr.toLowerCase() === stateAbbr.toLowerCase(),
  );
}

export const STATE_CODES = STATE_REGULATIONS.map((state) => state.abbr.toLowerCase());
