"use client";

import { useMemo, useState } from "react";
import { useRouter } from "next/navigation";
import { Search } from "lucide-react";
import { getTranslations, type SupportedLocale } from "@/data/translations";
import { STATE_REGULATIONS } from "@/data/states";
import { CITY_PROFILES } from "@/data/cities";

export function StateFinder({ locale }: { locale: SupportedLocale }) {
  const router = useRouter();
  const t = getTranslations(locale);
  const [query, setQuery] = useState("");

  const suggestions = useMemo(() => {
    if (!query.trim()) return [];
    const normalized = query.toLowerCase();
    const stateMatches = STATE_REGULATIONS.filter(
      (state) =>
        state.name.toLowerCase().includes(normalized) ||
        state.abbr.toLowerCase().includes(normalized),
    ).map((state) => ({
      label: `${state.name} (${state.abbr})`,
      href: `${locale === "es" ? "/es" : ""}/states/${state.abbr.toLowerCase()}`,
    }));

    const cityMatches = CITY_PROFILES.filter((city) =>
      city.city.toLowerCase().includes(normalized),
    ).map((city) => ({
      label: `${city.city}, ${city.stateAbbr}`,
      href: `${locale === "es" ? "/es" : ""}/cities/${city.stateAbbr.toLowerCase()}/${city.slug}`,
    }));

    return [...stateMatches, ...cityMatches].slice(0, 6);
  }, [locale, query]);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (!suggestions[0]) return;
    router.push(suggestions[0].href);
  };

  return (
    <section className="card-glass relative mx-auto max-w-3xl rounded-3xl p-8 shadow-xl">
      <div className="flex items-start gap-4">
        <div className="flex-1">
          <h2 className="text-2xl font-semibold text-slate-900">{t.finder.title}</h2>
          <form onSubmit={handleSubmit} className="mt-6 flex items-center gap-3 rounded-2xl border border-slate-200 bg-white/80 px-4 py-3 shadow-inner">
            <Search className="size-5 text-slate-400" aria-hidden />
            <input
              value={query}
              onChange={(event) => setQuery(event.target.value)}
              placeholder={t.finder.placeholder}
              className="flex-1 border-none bg-transparent text-sm text-slate-700 focus:outline-none"
            />
            <button
              type="submit"
              className="rounded-full bg-blue-500 px-4 py-2 text-sm font-semibold text-white shadow-lg"
            >
              {t.finder.button}
            </button>
          </form>
          {suggestions.length > 0 && (
            <ul className="mt-4 space-y-2 text-sm text-slate-600">
              {suggestions.map((item) => (
                <li key={item.href}>
                  <button
                    type="button"
                    onClick={() => router.push(item.href)}
                    className="w-full text-left rounded-2xl border border-slate-200 bg-white/70 px-4 py-3 hover:border-blue-300 hover:bg-blue-50"
                  >
                    {item.label}
                  </button>
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>
    </section>
  );
}
